:root {
    --white-font-clolr: #ffffff;
    --color-high-text: #005389;
    --color-tint: #005389;
    --color-background: #fff;
    --home-content-background: #034db6;
    --home-element-background: #034db6;
    --body-font-size: 12px;
    --line-height: 1.5;
}

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}

body {
    user-select: none;
    /* / */
    -webkit-tap-highlight-color: transparent;
    /* webkittaphighlightcolor */
    background: var(--color-background);
    color: var(--color-text);
    /* rem vw/vh */
    width: 100vw;font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","\5FAE\8F6F\96C5\9ED1",Arial,sans-serif;
}

a {
    color: var(--color-text);
    text-decoration: none;
}

button,
input,
select,
textarea {
    border: none;
    margin: 0;
    outline: none;
    padding: 0;
    background: none;
}

button {
    -webkit-appearance: button;
    -webkit-writing-mode: horizontal-tb!important;
    text-rendering: auto;
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0;
    text-shadow: none;
    display: inline-block;
    text-align: center;
    align-items: flex-start;
    cursor: default;
    box-sizing: border-box;
    margin: 0;
}

img {
    border: none;
    display: block;
}

::-webkit-scrollbar {
    display: none;
}

p.img {
  font-size: 0;
}

