  color: #ee0a24;
  font-size: 0.16rem;
}

.van-sku__stock {
  display: inline-block;
  margin-right: 0.106667rem;
  color: #969799;
  font-size: 0.16rem;
}

.van-sku__stock-num--highlight {
  color: #ee0a24;
}

.van-sku-messages {
  padding-bottom: 0.426667rem;
}

.van-sku-messages__image-cell .van-cell__title {
  max-width: 6.2em;
  margin-right: 0.16rem;
  color: #646566;
  text-align: left;
  word-wrap: break-word;
}

.van-sku-messages__image-cell .van-cell__value {
  overflow: visible;
  text-align: left;
}

.van-sku-messages__image-cell-label {
  color: #969799;
  font-size: 0.16rem;
  line-height: 0.24rem;
}

.van-sku-messages__cell-block {
  position: relative;
}

.van-sku-messages__cell-block::after {
  position: absolute;
  box-sizing: border-box;
  content: ' ';
  pointer-events: none;
  right: 0.213333rem;
  bottom: 0;
  left: 0.213333rem;
  border-bottom: 0.013333rem solid #ebedf0;
  -webkit-transform: scaleY(.5);
  transform: scaleY(.5);
}

.van-sku-messages__cell-block:last-child::after {
  display: none;
}

.van-sku-messages__extra-message {
  margin-top: -0.026667rem;
  padding: 0 0.213333rem 0.16rem;
  color: #969799;
  font-size: 0.16rem;
  line-height: 0.24rem;
}

.van-sku-actions {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-shrink: 0;
  flex-shrink: 0;
  padding: 0.106667rem 0.213333rem;
}

.van-sku-actions .van-button {
  height: 0.533333rem;
  font-weight: 500;
  font-size: 0.186667rem;
  border: none;
  border-radius: 0;
}

.van-sku-actions .van-button:first-of-type {
  border-top-left-radius: 0.266667rem;
  border-bottom-left-radius: 0.266667rem;
}

.van-sku-actions .van-button:last-of-type {
  border-top-right-radius: 0.266667rem;
  border-bottom-right-radius: 0.266667rem;
}

.van-sku-actions .van-button--warning {
  background: -webkit-linear-gradient(left,#ffd01e,#ff8917);
  background: linear-gradient(to right,#ffd01e,#ff8917);
}

.van-sku-actions .van-button--danger {
  background: -webkit-linear-gradient(left,#ff6034,#ee0a24);
  background: linear-gradient(to right,#ff6034,#ee0a24);
}

.van-slider {
  position: relative;
  width: 100%;
  height: 0.026667rem;
  background-color: #ebedf0;
  border-radius: 13.32rem;
  cursor: pointer;
}

.van-slider::before {
  position: absolute;
  top: -0.106667rem;
  right: 0;
  bottom: -0.106667rem;
  left: 0;
  content: '';
}

.van-slider__bar {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #1989fa;
  border-radius: inherit;
  -webkit-transition: all .2s;
  transition: all .2s;
}

.van-slider__button {
  width: 0.32rem;
  height: 0.32rem;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0 0.013333rem 0.026667rem rgba(0,0,0,.5);
}

.van-slider__button-wrapper,
.van-slider__button-wrapper-right {
  position: absolute;
  top: 50%;
  right: 0;
  -webkit-transform: translate3d(50%,-50%,0);
  transform: translate3d(50%,-50%,0);
  cursor: grab;
}

.van-slider__button-wrapper-left {
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translate3d(-50%,-50%,0);
  transform: translate3d(-50%,-50%,0);
  cursor: grab;
}

.van-slider--disabled {
  cursor: not-allowed;
  opacity: .5;
}

.van-slider--disabled .van-slider__button-wrapper,
.van-slider--disabled .van-slider__button-wrapper-left,
.van-slider--disabled .van-slider__button-wrapper-right {
  cursor: not-allowed;
}

.van-slider--vertical {
  display: inline-block;
  width: 0.026667rem;
  height: 100%;
}

.van-slider--vertical .van-slider__button-wrapper,
.van-slider--vertical .van-slider__button-wrapper-right {
  top: auto;
  right: 50%;
  bottom: 0;
  -webkit-transform: translate3d(50%,50%,0);
  transform: translate3d(50%,50%,0);
}

.van-slider--vertical .van-slider__button-wrapper-left {
  top: 0;
  right: 50%;
  left: auto;
  -webkit-transform: translate3d(50%,-50%,0);
  transform: translate3d(50%,-50%,0);
}

.van-slider--vertical::before {
  top: 0;
  right: -0.106667rem;
  bottom: 0;
  left: -0.106667rem;
}

.van-steps {
  overflow: hidden;
  background-color: #fff;
}

.van-steps--horizontal {
  padding: 0.133333rem 0.133333rem 0;
}

.van-steps--horizontal .van-steps__items {
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  margin: 0 0 0.133333rem;
  padding-bottom: 0.293333rem;
}

.van-steps--vertical {
  padding: 0 0 0 0.426667rem;
}

.van-swipe-cell {
  position: relative;
  overflow: hidden;
  cursor: grab;
}

.van-swipe-cell__wrapper {
  -webkit-transition-timing-function: cubic-bezier(.18,.89,.32,1);
  transition-timing-function: cubic-bezier(.18,.89,.32,1);
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform,-webkit-transform;
}

.van-swipe-cell__left,
.van-swipe-cell__right {
  position: absolute;
  top: 0;
  height: 100%;
}

.van-swipe-cell__left {
  left: 0;
  -webkit-transform: translate3d(-100%,0,0);
  transform: translate3d(-100%,0,0);
}

.van-swipe-cell__right {
  right: 0;
  -webkit-transform: translate3d(100%,0,0);
  transform: translate3d(100%,0,0);
}

.van-tabbar {
  z-index: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  box-sizing: content-box;
  width: 100%;
  height: 0.666667rem;
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom);
  background-color: #fff;
}

.van-tabbar--fixed {
  position: fixed;
  bottom: 0;
  left: 0;
}

.van-tabbar--unfit {
  padding-bottom: 0;
}